<!--  -->
<template>
  <div>
    <div class="table-btn-wrapper">
      <el-button type="info" size="small" round></el-button>
    </div>
    <data-table :dataList="meetings" :columnData="columnData" :expandColumn="expandColumn">
      <template slot-scope="props">
        <el-dropdown-item @click.native="removeMeeting(props.index, props.rowData)"></el-dropdown-item>
        <!--<el-dropdown-item @click.native="exportMeeting(props.index, props.rowData)"></el-dropdown-item>-->
      </template>
    </data-table>
  </div>
</template>
<script>/* eslint-disable indent,semi */
import DataTable from '../../components/DataTable.vue';

export default {
  data() {
    return {
      meetings: [],
      columnData: [
        {data: 'title', label: ''},
        {data: 'creator.userName', label: ''},
        {data: 'createTime', label: ''},
        {data: 'meetingRoom', label: ''},
        {data: 'startTime', label: ''},
        {data: 'endTime', label: ''},
        {data: 'status', label: '', formatter: (row, col) => row.status === 1 ? '' : ''},
      ],
      expandColumn: [
        {data: 'title', label: ''},
        {data: 'creator.userName', label: ''},
        {data: 'createTime', label: ''},
        {data: 'meetingRoom', label: ''},
        {data: 'startTime', label: ''},
        {data: 'endTime', label: ''},
        {data: 'desc', label: ''},
        {data: 'status', label: '', formatter: (row, col) => row.status === 1 ? '' : ''},
      ]
    };
  },
  components: {
    DataTable,
  },

  computed: {},

  created() {
    this.initData();
  },

  mounted() {
  },

  methods: {
    initData() {
      this.$http.get('/api/meeting/getMeetingList').then((res) => {
        let data = res.data;
        this.meetings = data.data;

      });
    },
//    getMeeting() {
//
//    },
//    exportMeeting() {
//
//    },
    removeMeeting(index, rowData) {
      let _this = this;
      let meetingId = rowData._id;
      this.$confirm('', '').then(() => {
        this.$http.post('/api/meeting/removeMeeting', {meetingId: meetingId}).then(res => {
          this.$message({
            message: '',
            duration: 1500,
            type: 'success',
          });
          _this.initData();
        });
      }).catch(() => {
      });
    }
  }
};
</script>

