<!--  -->
<template>
  <div>
    <div class="table-btn-wrapper">
      <el-button size="small" type="info"></el-button>
    </div>
    <data-table :dataList="leaves" :columnData="columnData">
      <template slot-scope="props">
        <el-dropdown-item @click.native="getLeave(props.index, props.rowData)"></el-dropdown-item>
        <!--<el-dropdown-item @click.native="exportLeave(props.index, props.rowData)"></el-dropdown-item>-->
        <el-dropdown-item @click.native="removeLeave(props.index, props.rowData)"></el-dropdown-item>
      </template>
    </data-table>
  </div>
</template>
<script>/* eslint-disable indent,semi */
import DataTable from '../../components/DataTable.vue';

export default {
  data() {
    return {
      leaves: [],
      columnData: [
        {data: 'creator.userName', label: ''},
        {data: 'leaveType', label: ''},
        {data: 'leaveTitle', label: ''},
        {data: 'leaveTime', label: ''},
        {data: 'status', label: '', formatter: (row, col) => row.status === 0 ? '' : ''},
        {data: 'startTime', label: ''},
        {data: 'endTime', label: ''},
      ],
    };
  },
  components: {
    DataTable,
  },

  computed: {},

  created() {
    this.initData();
  },

  mounted() {
  },

  methods: {
    initData() {
      this.$http.get('/api/ask-leave/getAskLeaveList').then((res) => {
        let data = res.data;
        this.leaves = data.data;
      });
    },
    getLeave(index, rowData) {
      this.$router.push({
        path: '/ask-leave/detail',
        query: {
          leaveId: rowData._id,
          type: 'detail'
        }
      });
    },
    removeLeave(index, rowData) {
      let _this = this;
      let leaveId = rowData._id;
      this.$confirm('', '').then(() => {
        this.$http.post('/api/ask-leave/removeAskLeave', {leaveId}).then(res => {
          this.$message({
            message: '',
            duration: 1500,
            type: 'success',
          });
          _this.initData();
        });
      }).catch(() => {
      });
    },
    exportLeave(index, rowData) {

    },
  }
};
</script>

