<!--  -->
<template>
  <div>
    <bread-crumb>
      <template>
        <el-breadcrumb-item :to="{ path: '/user' }"></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </template>
    </bread-crumb>
    <div class="up-btn-area">
      <el-upload
        class="upload-demo"
        ref="upload"
        accept=".xls,.xlsx"
        action="https://jsonplaceholder.typicode.com/posts/"
        :on-change="upload"
        :show-file-list="false"
        :auto-upload="false">
        <el-button size="small" type="primary"></el-button>
        <div slot="tip" class="el-upload__tip">xls/xlsx</div>
      </el-upload>
    </div>
    <div>
      <div class="table-title"></div>
      <data-table :dataList="outData"
                  :columnData="columnData" :expandColumn="expandColumn"
                  :showOpa="false" :showSelect="false">

      </data-table>
      <div class="import-btn-area">
        <el-button type="success" @click="importUser"></el-button>
        <el-button type="info" @click="reset"></el-button>
      </div>
    </div>

    <div class="up-btn-area">
      <el-upload
        class="upload-demo"
        ref="upload"
        accept=".xls,.xlsx"
        action="https://jsonplaceholder.typicode.com/posts/"
        :on-change="uploadDept"
        :show-file-list="false"
        :auto-upload="false">
        <el-button size="small" type="primary"></el-button>
        <div slot="tip" class="el-upload__tip">xls/xlsx</div>
      </el-upload>
    </div>
    <div>
      <div class="table-title"></div>
      <data-table :dataList="deptOutData"
                  :columnData="deptColumnData"
                  :showOpa="false" :showSelect="false">

      </data-table>
      <div class="import-btn-area">
        <el-button type="success" @click="importDept"></el-button>
        <el-button type="info" @click="resetDept"></el-button>
      </div>
    </div>
  </div>
</template>
<script>/* eslint-disable indent,semi */
import XLSX from 'xlsx';
import BreadCrumb from '../../components/BreadCrumb.vue';
import DataTable from '../../components/DataTable.vue';

export default {
  data() {
    return {
      minValue: '', // v-model
      oldMinValue: '', // minValue

      outData: [],
      deptOutData: [],
      columnData: [
        {data: 'userName', label: ''},
        {data: 'deptName', label: ''},
        {data: 'orgName', label: ''},
        {data: 'postName', label: ''},
        {data: 'tel', label: ''},
        {data: 'pwd', label: ''},
        {data: 'email', label: ''},
        {data: 'remark', label: ''},
      ],
      expandColumn: [
        {data: 'userName', label: ''},
        {data: 'dept', label: ''},
        {data: 'org', label: ''},
        {data: 'post', label: ''},
        {data: 'tel', label: ''},
        {data: 'pwd', label: ''},
        {data: 'email', label: ''},
        {data: 'remark', label: ''},
      ],
      deptColumnData: [
        {data: 'deptName', label: ''},
        {data: 'deptNo', label: ''},
        {data: 'parentDeptName', label: ''},
        {data: 'desc', label: ''},
      ],
    };
  },
  components: {
    BreadCrumb,
    DataTable,
  },

  computed: {},

  created() {
  },

  mounted() {
  },

  methods: {
    importUser() {
      this.$confirm('', '').then(() => {
        return this.$http.post(`/api/user/importUser`, {users: this.outData});
      }).then((res) => {
      });

    },
    importDept() {
      this.$confirm('', '').then(() => {
        return this.$http.post(`/api/user/importDept`, {depts: this.deptOutData});
      }).then((res) => {
      });

    },
    reset() {
      this.outData = [];
    },
    resetDept() {
      this.deptOutData = [];
    },
    upload(file, fileList) {
      console.log('file', file);
      console.log('fileList', fileList);
      let files = {0: file.raw};
      this.readExcel1(files, 'user');
    },
    uploadDept(file, fileList) {
      console.log('file', file);
      console.log('fileList', fileList);
      let files = {0: file.raw};
      this.readExcel1(files, 'dept');
    },
    readExcel1(files, type) {
      let _this = this;
      //
      console.log(files, type);
      if (files.length <= 0) {//
        return false;
      } else if (!/\.(xls|xlsx)$/.test(files[0].name.toLowerCase())) {
        this.$message({
          message: '',
          type: 'warning'
        });
        return false;
      }
      let f = files[0];
      const reader = new FileReader();
      FileReader.prototype.readAsBinaryString = function (f) {
        let binary = '';
        let wb; // 
        let outdata; // 
        let reader = new FileReader();
        reader.onload = function (e) {
          // Uint8ArrayUnicodeUnicode
          let bytes = new Uint8Array(reader.result);
          let length = bytes.byteLength;
          for (let i = 0; i < length; i++) {
            binary += String.fromCharCode(bytes[i]);
          }
          // xlsxapi
          wb = XLSX.read(binary, {
            type: 'binary'
          });
          outdata = XLSX.utils.sheet_to_json(wb.Sheets[wb.SheetNames[0]]);
          if (type === 'user') {

            _this.outData = outdata.slice(1);
            // 
//          let nameReg =  /^\(.+?$\)/g;
            _this.outData.forEach((item) => {
              if (item.dept) {
                let data = item.dept.split(' | ');
                item.deptName = data[1];
                item.dept = data[0];
              }
              if (item.org) {
                let data = item.org.split(' | ');
                item.orgName = data[1];
                item.org = data[0];
              }
              if (item.post) {
                let data = item.post.split(' | ');
                item.postName = data[1];
                item.post = data[0];
              }
            });
          }else{
            _this.deptOutData = outdata.slice(1);
            _this.deptOutData.forEach((item) => {
              if (item.parentDept) {
                let data = item.parentDept.split(' | ');
                item.parentDeptName = data[1];
                item.parentDept = data[0];
              }
            });
          }
        };
        reader.readAsArrayBuffer(f);
      };
      reader.readAsBinaryString(f);
    },

  }
};
</script>

