<!--  -->
<template>
  <div>
    <div class="operate-wrapper">
      <el-button type="success" size="small" round @click="$router.push({path:'/form-flow/design',query:{type:'add'}})"></el-button>
      <!-- <el-button type="danger" size="small" round @click="batchDeleteUser"></el-button> -->
    </div>
    <data-table :dataList="forms" :columnData="columnData">
      <template slot-scope="props">
        <el-dropdown-item @click.native="editForm(props.index, props.rowData)"></el-dropdown-item>
        <el-dropdown-item @click.native="delForm(props.index, props.rowData)"></el-dropdown-item>
      </template>
      <!--<el-dropdown-item></el-dropdown-item>-->
      <!--<el-dropdown-item></el-dropdown-item>-->
    </data-table>
  </div>
</template>
<script>
  /* eslint-disable indent,semi */
  import DataTable from "../../components/DataTable.vue";

  export default {
    data() {
      return {
        rowData: [],
        forms: [],
        columnData: [
          {data: "formName", label: ""},
          {data: "creator", label: ""},
          {data: "formGroup", label: ""},
          {
            data: "formType",
            label: "",
            formatter: (row, col) => row.formType === 0 ? '' : row.formType === 1 ? '' : ''
          },
          {data: "data", label: ""},
          {data: "status", label: "", formatter: (row, col) => row.status === 0 ? '' : ''}
        ]
      };
    },
    components: {
      DataTable
    },

    computed: {},

    created() {
      this.initData();
    },

    mounted() {
    },

    methods: {
      initData() {
        this.$http.get('/api/form/getFormList').then((res) => {
          let data = res.data.data;
          this.forms = data;
        });
      },
      editForm(index, rowData) {
        this.$router.push({
          path: "/form-flow/design",
          query: {
            formId: rowData._id,
            type: "edit"
          }
        });
      },
      delForm(index, rowData) {
        this.$confirm('', '').then(() => {
          let formId = rowData._id;
          this.$http.post('/api/form/removeForm', {formId}).then((res) => {
            this.$message({
              message: '',
              duration: 1500,
              type: 'success',
            });
            this.initData();
          });
        }).catch(() => {

        });
      }
    }
  };
</script>

<style scoped>
  .operate-wrapper {
    margin-bottom: 1.5rem;
    display: flex;
  }

  .more-handle {
    text-align: center;
  }

  .icon-more-handle {
    font-size: 20px;
    text-align: center;
    color: #969896;
  }

  .icon-more-handle:hover {
    color: #00b7ff;
  }
</style>
