const clickoutside = {
  // 
  bind(el, binding, vnode) {
    function clickHandler(e) {
      // 
      if (el.contains(e.target)) {
        return false;
      }
      // 
      if (binding.expression) {
        //  binding.valuehandleClose
        binding.value(e);
      }
    }
    // unbind
    el.__vueClickOutside__ = clickHandler;
    document.addEventListener('click', clickHandler);
  },
  update() {},
  unbind(el, binding) {
    // 
    document.removeEventListener('click', el.__vueClickOutside__);
    delete el.__vueClickOutside__;
  },
};

export const fieldMixin = {
  name:'fieldMixin',
  directives: {clickoutside},
  props:{
    fieldIndex:Number
  },
  data(){
    return{
      visible: false, //
    }
  },
  computed:{
    fieldData: {
      get: function () {
        return this.fieldItemData;
      },
      set: function (val) {
        this.fieldItemData = val;
      }
    }
  },
  created(){

  },
  mounted(){
    this.$emit('update:fieldItemData', this.fieldData);
  },
  methods:{
    handleClose(e) {
      this.visible = false;
    },
    copyField(){
      this.$emit('copyField',this.fieldIndex);
    },
    removeField(){
      this.$emit('removeField',this.fieldIndex);
    }
  },
  watch:{
    fieldData: {
      handler(newData) {
        this.$emit('update:fieldItemData', newData);
      },
      deep: true
    }
  }
};

