Array.prototype.pushNoRepeat = function () {
  let flag = false;
  for (let i = 0; i < arguments.length; i++) {
    let ele = arguments[i];
    if (this.indexOf(ele) === -1) {
      this.push(ele);
      flag = true;
    }
  }
  return flag
};
Array.prototype.ToList = function () {
  if(!this.length) {
    return null
  }

  let node = {};
  let head = {data: this[0], childNode: null};
  let pNode = head;  //pnode

  for(let i = 1; i < this.length; i++) {
    node = {data: this[i], childNode:null};
    pNode.childNode = node;   //next
    pNode = node;   //nodepnode
  }
  return head;
};
function Array2List(ary) {
  if(!ary.length) {
    return null
  }

  let node = {};
  let head = {value: ary[0], next: null};
  let pNode = head;  //pnode

  for(let i = 1; i < ary.length; i++) {
    node = {value: ary[i], next:null};
    pNode.next = node;   //next
    pNode = node;   //nodepnode
  }
  return head;
}

