<!--  -->
<template>
  <div>
    <div class="operate-wrapper">
      <el-button type="success" size="small" round @click="$router.push({path:'fixed-flow/detail',query:{type:'add'}})"></el-button>
      <!-- <el-button type="danger" size="small" round @click="batchDeleteUser"></el-button> -->
    </div>
    <data-table :dataList="flows" :columnData="columnData">
      <template slot-scope="props">
        <el-dropdown-item v-if="props.rowData.status === 0" @click.native="addFlow(props.index, props.rowData)"></el-dropdown-item>
        <el-dropdown-item v-else @click.native="saveDraft(props.index, props.rowData)"></el-dropdown-item>
        <el-dropdown-item @click.native="editFlow(props.index, props.rowData)"></el-dropdown-item>
        <el-dropdown-item @click.native="delFlow(props.index, props.rowData)"></el-dropdown-item>
      </template>
    </data-table>
  </div>
</template>
<script>/* eslint-disable indent,semi */
//import BreadCrumb from '../../components/BreadCrumb.vue'
import DataTable from '../../components/DataTable.vue'

export default {
  data() {
    return {
      columnData: [
        {data: "flowName", label: ""},
        {data: "flowGroup", label: ""},
        {data: "createTime", label: ""},
        {data: "status", label: "",formatter:(row,col)=> row.status === 1 ? '':''}
      ],
      flows: [],
    };
  },
  components: {
//    BreadCrumb,
    DataTable,
  },

  computed: {},

  created() {
    this.initData();
  },

  mounted() {
  },

  methods: {
    initData(){
      this.$http.get('/api/fixed-flow/getFixedFlowList').then((res)=>{
        let data = res.data.data;
        this.flows = data;
      })
    },
    saveDraft(index,data){
      data.status = 0;
      this.$http.post('/api/fixed-flow/updateFixedFlow', {flowData: data}).then((res) => {
        this.$message({
          message: '',
          duration: 1500,
          type: 'success',
        });
        this.initData();
      });
    },
    addFlow(index,data){
      data.status = 1;
      this.$http.post('/api/fixed-flow/updateFixedFlow', {flowData: data}).then((res) => {
        this.$message({
          message: '',
          duration: 1500,
          type: 'success',
        });
        this.initData();
      });
    },
    editFlow(index,data){
      let flowId = data._id;
      this.$router.push({
        path:'fixed-flow/detail',
        query:{
          flowId:flowId,
          type:'edit'
        }
      });
    },
    delFlow(index,data){
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        let flowId = data._id;
        this.$http.post('/api/fixed-flow/removeFixedFlow',{flowId}).then((res)=>{
          this.$message({
            message: '',
            duration:1500,
            type: 'success',
          });
          this.initData();
        });
      }).catch(() => {
//        this.$message({
//          type: 'info',
//          duration:1500,
//          message: ''
//        });
      });

    }
  }
}
</script>

<style scoped>
  .operate-wrapper {
    margin-bottom: 1.5rem;
    display: flex;
  }
</style>
