<!--  -->
<template>
  <div>
    <bread-crumb>
      <el-breadcrumb-item :to="{ path: '/ask-leave/setting' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </bread-crumb>
    <div class="table-btn-wrapper">
      <el-button size="small" type="success" round @click="addType"></el-button>
    </div>
    <data-table :dataList="types" :columnData="columnData">
      <template slot-scope="props">
        <el-dropdown-item @click.native="editType(props.index, props.rowData)"></el-dropdown-item>
        <el-dropdown-item @click.native="delType(props.index, props.rowData)"></el-dropdown-item>
      </template>
    </data-table>
  </div>

</template>
<script>/* eslint-disable indent,semi */
import DataTable from '../../components/DataTable.vue';
import BreadCrumb from '../../components/BreadCrumb.vue';

export default {
  data() {
    return {
      types: [],
      columnData: [
        {data: 'typeName', label: ''},
        {data: 'typeTitle', label: ''},
        {data: 'createTime', label: ''},
        {data: 'status', label: '', formatter: (row, col) => row.status === 0 ? '' : ''},
      ],
    };
  },
  components: {
    DataTable,
    BreadCrumb,
  },

  computed: {},

  created() {
    this.initData();
  },

  mounted() {
  },

  methods: {
    initData() {
      this.$http.get('/api/ask-leave/getLeaveTypeList').then((res) => {
        let data = res.data;
        this.types = data.data;
      });
    },
    addType(){
      this.$router.push({
        path: 'type/detail',
        query: {
          type: 'add'
        }
      });
    },
    editType(index, rowData) {
      this.$router.push({
        path: 'type/detail',
        query: {
          typeId: rowData._id,
          type: 'edit'
        }
      });
    },
    delType(index, rowData) {
      this.$confirm('', '').then(() => {
        let typeId = rowData._id;
        this.$http.post('/api/ask-leave/removeLeaveType', {typeId}).then((res) => {
          this.$message({
            message: '',
            duration: 1500,
            type: 'success',
          });
          this.initData();
        });
      }).catch(() => {

      });
    },
  }
};
</script>

